@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+JP:400&subset=japanese");
@font-face {
  font-family: "Orkney";
  font-style: normal;
  font-weight: 400;
  src: url("../font/OrkneyRegular.woff") format("woff");
}
@font-face {
  font-family: "Orkney";
  font-style: italic;
  font-weight: 400;
  src: url("../font/OrkneyRegularItalic.woff") format("woff");
}
@font-face {
  font-family: "Orkney";
  font-style: normal;
  font-weight: 700;
  src: url("../font/OrkneyBold.woff") format("woff");
}
@font-face {
  font-family: "Mazius";
  font-style: normal;
  font-weight: 400;
  src: url("../font/MaziusDisplayRegular.woff") format("woff");
}
@font-face {
  font-family: "PP Eiko Italic";
  font-style: italic;
  font-weight: 100;
  src: url("../font/PPEiko-LightItalic.woff") format("woff");
}
.jp, .jp * {
  font-size: 11px;
  font-family: "Noto Sans JP";
  font-weight: 400;
  letter-spacing: -0.03em;
  line-height: 1.6;
}

.en, .en * {
  font-size: calc(11px + -1 * (100vw - 480px) / 520);
  font-family: "Orkney";
  font-weight: 400;
  line-height: 1.6;
}

@media (min-width: 480px) {
  .jp, .jp * {
    font-size: calc(11px + -1 * (100vw - 480px) / 520);
  }
}
@media (min-width: 1000px) {
  .jp, .jp * {
    font-size: 10px;
  }
}
.info {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0 auto;
  margin-bottom: 6rem;
  max-width: 90rem;
}

.project-nickname {
  line-height: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 2rem;
}
.project-nickname::before {
  content: "●";
  display: block;
  width: 1em;
  height: 1em;
  margin-right: 0.4rem;
}

.project-role-year time {
  display: block;
  padding-top: 2rem;
}

.info--title h3 {
  font-weight: 700;
  margin-bottom: 0.6em;
  text-transform: uppercase;
}
.info--title.new {
  margin: 6rem 0;
}
.info--title.new h1 {
  font-family: "PP Eiko Italic";
  font-size: 10.2vw;
  line-height: 1.2;
  text-align: right;
  text-transform: uppercase;
}

.info--desc p {
  margin: 3em 0 2em;
}
.info--desc span:nth-of-type(2) {
  display: block;
  padding-top: 0.8em;
}

.project-description p {
  margin: 0;
}
.project-description p:last-of-type {
  margin-top: 2rem;
}
.project-description p:not(.jp) {
  text-transform: uppercase;
}

.image {
  display: grid;
  grid-template-columns: 1fr;
  position: relative;
  margin: calc(var(--gap) * -1);
}
.image figure {
  height: calc((100vw - var(--gutter) * 2) / 1.618);
  background: black;
  margin: var(--gap);
  padding: 10%;
  overflow: hidden;
}
.image figure.h0 {
  height: auto !important;
}
.image figure.p0 {
  padding: 0;
}
.image .spacer-2 {
  display: none;
}

@media screen and (min-width: 768px) {
  .info {
    grid-auto-columns: min-content;
    padding-left: 50%;
  }
  .info.new {
    padding-left: 0;
  }
  .info.new .project-intro {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    flex-shrink: 1;
    gap: var(--gutter);
  }
  .info.new .project-intro div {
    width: 50%;
  }
  .info--title.new h1 {
    font-size: 6.25vw;
    padding-right: var(--gap);
    padding-left: calc(25% + var(--gap));
  }
  .project-description {
    margin-right: calc(25% + var(--gap));
    display: flex;
    flex-direction: row;
    gap: var(--gutter);
  }
  .project-description p {
    flex: 1;
  }
  .project-description p:last-of-type {
    margin-top: 0;
  }
  .image {
    grid-template-columns: 1fr;
  }
  .image figure.c-1 {
    height: calc((100vw - var(--gutter) * 2) / 1.618);
  }
  .image figure.c-2 {
    height: calc((100vw - var(--gutter) * 2 - var(--gap)) / 2 / 1.618);
  }
  .image .spacer-2 {
    display: block;
    grid-column: auto/span 3;
  }
}
:root {
  --gutter:8vw;
  --alley:var(--gutter);
  --gap:3.2vw;
  --column:6;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
*::after, *::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

img {
  display: block;
  object-fit: contain;
  width: 100%;
  height: 100%;
}

ul, li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p, ul, li, a, b, sup, sub, time, figure {
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  color: inherit;
  line-height: inherit;
  vertical-align: inherit;
  transition: inherit;
  cursor: default;
}

a {
  text-decoration: none;
  cursor: pointer;
}
a:visited {
  color: inherit;
}

::-moz-selection, ::selection, ::-moz-selection {
  color: white;
  background: black;
}

::-webkit-scrollbar {
  display: none;
}

html, body {
  position: relative;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  font-size: 62.5%;
  font-family: "Orkney", sans-serif;
  font-weight: 400;
  color: #000;
  background-color: #FFA360;
  line-height: 1;
  text-rendering: optimizeLegibility;
  scrollbar-width: none;
  overflow-wrap: break-word;
}

a {
  font-family: "Mazius", serif;
  font-size: 8vw;
  letter-spacing: -0.03em;
  margin: -0.13em 0;
}

p, h3, .kyoko {
  font-size: 11px;
  line-height: 1.35;
}

.footer, time, .list--head li {
  font-size: 9px;
}

.list--list li::before, .list--list li::after {
  font-size: 9px;
}

@media (min-width: 480px) {
  .footer, time, .list--head li {
    font-size: calc(9px + -1 * (100vw - 480px) / 520);
  }
  .list--list li::before, .list--list li::after {
    font-size: calc(9px + -1 * (100vw - 480px) / 520);
  }
}
@media (min-width: 1000px) {
  .footer, time, .list--head li {
    font-size: 8px;
  }
  .list--list li::before, .list--list li::after {
    font-size: 8px;
  }
}
svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.header, .footer, .home, .dots div, .title, .page {
  transition-delay: 0.1s;
  transition-timing-function: ease-out;
  transition: padding 0.5s, margin 0.5s, grid-column 0.5s, width 0.5s, height 0.5s;
}

a {
  transition-delay: 0.1s;
  transition-timing-function: ease-out;
  transition: font-size 0.5s;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: var(--gutter);
  z-index: 999;
  pointer-events: none;
}
.header .header__wrap {
  height: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: auto;
}
.header .header__wrap li {
  pointer-events: auto;
}
.header .header__wrap li.kyoko {
  line-height: 0;
  font-weight: 700;
}

.arrow {
  display: flex;
  width: 0.7em;
  height: 0.7em;
  margin-bottom: 0.02em;
  margin-right: 0.5em;
  transform: rotate(-90deg);
}

.header--link {
  font-family: "Mazius", serif;
  display: inline-block;
  margin: var(--gutter) var(--gutter) 0;
  overflow: hidden;
}
.header--link a {
  display: flex;
  align-items: center;
  opacity: 0.5;
}
.header--link a .arrow {
  transform: rotate(0);
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0 var(--gutter) var(--gutter);
}
.footer .footer__wrap {
  letter-spacing: -0.03em;
  height: 10px;
  display: flex;
}

.error {
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  color: white;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.home {
  position: relative;
  width: 100%;
  padding: 0 var(--alley);
  overflow: hidden;
}

.grid {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto min-content min-content;
  padding-bottom: var(--alley);
}

.title {
  align-self: center;
  margin: calc(var(--gutter) * 2 + 1rem) 0;
  font-size: 19.5vw;
  display: flex;
}
.title .title--title {
  font-family: "Mazius", serif;
  letter-spacing: -0.03em;
  display: flex;
  margin-top: -0.1em;
  margin-left: -0.03em;
  margin-bottom: -0.15em;
}
.title .title--title span {
  display: block;
}
.title .title--title span:nth-of-type(1) {
  margin-left: -0.06em;
}
.title .title--title span:nth-of-type(2) {
  padding-right: 0.05em;
}
.title .title--title span:nth-of-type(3) {
  padding-right: 0.015em;
}
.title .title--slash {
  margin-top: -0.33em;
  margin-bottom: -1.25em;
}
.title .title--slash *::after {
  transform-origin: 100% 100%;
  transform: rotate(30deg);
  display: block;
  content: "";
  height: 1.25em;
  width: 1px;
  background-color: currentColor;
  margin-left: -1px;
}
.title .title--year {
  grid-row: 2;
  grid-column: 2;
  align-self: flex-end;
  font-size: 0.3em;
  letter-spacing: -0.1em;
  display: flex;
  margin-left: 1.1em;
}
.title .title--year span {
  display: block;
  margin-bottom: -0.2em;
}
.title .title--year span:first-of-type {
  padding-right: 0.1em;
}

.bio {
  align-self: end;
  padding-bottom: var(--alley);
}
.bio span:nth-of-type(2) {
  display: block;
  padding-top: 0.8em;
}
.bio .bio--link {
  font-family: inherit;
  font-size: inherit;
  text-decoration: underline;
  display: inline-flex;
  align-items: center;
  font-weight: 700;
}

.scroll {
  font-family: "Mazius", serif;
  white-space: nowrap;
  -ms-flex-item-align: end;
  align-self: end;
  display: flex;
  align-items: center;
}

.list {
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.list--head {
  position: absolute;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.list--head li {
  height: 1rem;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  transform-origin: right;
  transform: rotate(90deg) translateX(50%) translateY(50%);
}
.list--head li span {
  display: block;
  width: 2em;
}

.list--list {
  align-self: center;
  margin: calc(var(--gutter) * 2) 0 calc(var(--gutter) * 2 + 1rem);
  counter-reset: num;
}
.list--list li {
  position: relative;
  display: flex;
  align-items: center;
}
.list--list li::before, .list--list li::after {
  display: none;
  font-size: 10px;
  white-space: nowrap;
  width: 1.2em;
  align-self: flex-end;
  position: absolute;
}
.list--list li:nth-of-type(odd)::before {
  counter-increment: num;
  content: counter(num, decimal-leading-zero);
  left: -2em;
}
.list--list li:nth-of-type(even)::after {
  counter-increment: num;
  content: counter(num, decimal-leading-zero);
  right: -2em;
}
.list--list li:not(:first-of-type) {
  margin-top: 2rem;
}
.list--list li a {
  position: relative;
}
.list--list li a time {
  display: none;
  position: absolute;
  bottom: 0;
  padding-left: 0.5em;
  white-space: nowrap;
}

.dots {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  pointer-events: none;
}
.dots div {
  width: 90px;
  height: 90px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
}
.dots div.d-a {
  top: -4%;
  left: -2%;
}
.dots div.d-b {
  top: 10%;
  left: 75%;
}
.dots div.d-c {
  top: 52%;
  left: 20%;
}
.dots div.d-d {
  top: 17%;
  left: 87%;
}

.page {
  position: relative;
  width: 100%;
  padding: 20rem var(--gutter);
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  :root {
    --gutter:7.5vw;
    --gap:2.5vw;
  }
  p, h3 {
    font-size: 11px;
  }
}
@media screen and (min-width: 480px) and (min-width: 480px) {
  p, h3 {
    font-size: calc(11px + 1 * (100vw - 480px) / 520);
  }
}
@media screen and (min-width: 480px) and (min-width: 1000px) {
  p, h3 {
    font-size: 12px;
  }
}
@media screen and (min-width: 480px) {
  a {
    font-size: 6.25vw;
  }
  .grid {
    grid-template-columns: repeat(6, 1fr);
  }
  .header--link {
    grid-template-columns: 1fr 1fr;
  }
  .title {
    grid-column: 1/span 6;
    font-size: 18.8vw;
  }
  .bio {
    grid-column: 4/span 3;
    padding-bottom: 0;
  }
  .scroll {
    grid-row: 2;
    grid-column: 2;
  }
  .list {
    justify-content: center;
  }
  .list--list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .list--list li::before, .list--list li::after {
    display: block;
  }
}
@media screen and (min-width: 768px) {
  :root {
    --gutter:6.25vw;
    --alley:8.33333vw;
    --gap:2.08333vw;
  }
  a {
    font-size: 4vw;
  }
  .bio {
    grid-column: 4/span 2;
  }
}
@media screen and (min-width: 878px) {
  :root {
    --gutter:5.23918vw;
  }
}
@media screen and (min-width: 1024px) {
  :root {
    --gutter:3.90625vw;
    --alley:6.25vw;
    --gap:1.5625vw;
  }
  .title {
    font-size: 16vw;
  }
}
@media screen and (min-width: 1280px) {
  h1 {
    font-size: 9rem;
  }
  p, h3, .kyoko {
    font-size: 0.9375vw;
  }
  .footer, time, .list--head li {
    font-size: 0.625vw;
  }
  .list--list li::before, .list--list li::after {
    font-size: 0.625vw;
  }
  .jp {
    font-size: 0.78125vw;
  }
  .jp * {
    font-size: 0.78125vw;
  }
}
